<script lang="ts">
  import { alert } from '../stores'

  function onClose() {
    alert.set('')
  }
</script>

<div class="alert" role="alert">
  <button
    class="close"
    data-dismiss="alert"
    type="button"
    aria-label="Close"
    on:click={onClose}
  >
    <span aria-hidden="true">&times;</span>
  </button>
  <p class="text-center">{$alert}</p>
</div>
